#navbar {
    top: 0;
    position: sticky;
    position: -webkit-sticky;
    z-index: 2;
    font-family: "Arvo", serif;
    color: white;
    background: linear-gradient(112.1deg, rgb(32, 38, 57) 11.4%, rgb(63, 76, 119) 70.2%);
}

#navbar .navbar-brand {
    font-family: "Arvo", serif;
}

#navbar .popover-content {
    background: rgb(32,38,57);
    background: linear-gradient(112deg, rgba(32,38,57,0.8) 0%, rgba(63,76,119,0.8) 100%, rgba(63,76,119,0.8) 100%);
}

#navbar .searchbar {
    z-index: 2;
    padding: 1em;
    background: rgb(32,38,57);
    background: linear-gradient(112deg, rgba(32,38,57,0.85) 0%, rgba(63,76,119,0.85) 100%, rgba(63,76,119,0.85) 100%);
    /* transition: all 1s ease-in-out !important; */
}

#navbar .searchbar .input-group {
    /* width: 70vw; */
    margin: 0 auto;
}