#home .banner{
    width: 100%;
    min-height: 80vh;
    background: #4e54c8;  
    background: linear-gradient(112.1deg, rgb(32, 38, 57) 11.4%, rgb(63, 76, 119) 70.2%);
}
/* 
#home .banner .container {
    height: 100%;
} */

#home .banner .maincontent {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

@media (max-width: 900px) {
    #home .banner .maincontent {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    #home .banner .avatar {
        margin-bottom: 1.5em;
    }
}

#home .banner .avatar {
    padding: 0.7em;
    cursor: pointer;
    border-radius: 50%;
    border-width: 0.3em; 
    border-style: solid;
}

#home .banner .avatar img {
    width: 17em;
    height: 17em;
    border-radius: 50%;
}

#home .banner .intro {
    font-family: "Arvo", serif;
}

#home .banner .social-icons * {
    margin-right: 0.3em;
}